.button {
  display: flex;
  justify-content: center;
  align-items: center;

  box-sizing: border-box;
  background-color: #fff;
  width: 100%;
  height: 56px;
  border-radius: 28px;
  border: none;

  color: #1d1d1d;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  text-align: center;
  text-decoration: none;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }
}
